import React, { useState } from 'react';

import { Drawer, Box, CssBaseline, Typography, Button, createTheme, ThemeProvider } from '@mui/material';
import TreeView from '@mui/lab/TreeView';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import ChevronRightIcon from '@mui/icons-material/ChevronRight';
import TreeItem from '@mui/lab/TreeItem';

const theme = createTheme({
    palette: {
        background: {
            paper: '#F5F4F6',
        }
    },
});


const drawerWidth = 240;

const Sidebar = ({ folders, openPaper }) => {

    const createFolder = () => {

    }

    const elements = folders.map(folder => {
        const { id, title } = folder;
        return (
            <TreeItem nodeId={id} label={title} onClick={() => openPaper(folder)} />
        )
    })

    return (
        <ThemeProvider theme={theme}>
            <CssBaseline />
            <Box
                component="nav"
                sx={{ width: { sm: drawerWidth }, flexShrink: { sm: 0 } }}
                aria-label="mailbox folders"
            >
                <Drawer
                    variant="permanent"
                    sx={{
                        display: { xs: 'none', sm: 'block' },
                        '& .MuiDrawer-paper': { boxSizing: 'border-box', width: drawerWidth },
                    }}
                    open
                >
                    <Box sx={{ ml: 2, mt: 5 }}>
                        <Typography sx={{ fontWeight: 'bold' }} variant='h5'>routini</Typography>
                        <TreeView
                            aria-label="multi-select"
                            defaultCollapseIcon={<ExpandMoreIcon />}
                            defaultExpandIcon={<ChevronRightIcon />}
                            multiSelect
                            sx={{ overflowY: 'auto', my: 3 }}
                        >
                            <TreeItem nodeId="1" label="–ó–∞–º—ñ—Ç–∫–∏">
                                {elements}
                            </TreeItem>
                        </TreeView>
                        <Button variant='outlined' onClick={createFolder} >–î–æ–¥–∞—Ç–∏ –ø–∞–ø–∫—É</Button>
                    </Box>
                </Drawer>
            </Box>


        </ThemeProvider>
    );
}

export default Sidebar;